<template>

  <div class="container">
    <my-title title="全部书籍" rightImg="/static/home_find.png"></my-title>
    <!--筛选框-->
    <div class="filter">
      <div class="filter-down" @click="downToogle">{{selectedClass}}</div>
      <div class="filter-read">最多阅读</div>
      <div class="filter-postil">最多批注</div>
    </div>
    <!---->
    <div class="body">

      <!--蒙砂-->
      <div class="mask" @click="downToogle">
        <!--下拉框-->
        <div class="down-box">
          <div style="width: 30%;padding: 10px 5px;color: #3e83a9;font-size: 0.9em;"
               v-for="(item,index) in classes"
               @click.stop="selectClass(index)">
            {{item.className}}
          </div>
        </div>
      </div>
      <!--书籍列表-->
      <div class="bookList" v-for="(book,index) in books">
        <!--左边图片-->
        <img class="left" :src="book.icon">

        <!--中间内容-->
        <div class="mid">
          <div style="font-size: 0.9em;color: #222">{{book.name}}</div>
          <div style="font-size: 0.8em;margin-top: 5px;color: #989898">{{book.writer}}</div>
          <div style="font-size: 0.7em;margin-top: 10px;color: #84cdf3">已有{{book.readPeopleNum}}人阅读</div>
        </div>

        <!--右边按钮-->
        <button class="right" @click="toBookDetail(index)">查看详情</button>

      </div>

    </div>

  </div>

</template>

<script>
  import MyTitle from '@/components/common/MyTitle'
  export default {
    name: 'AllBook',
    components:{MyTitle},
    data(){
      return{
        selectedClass: "全部类型",
        books:[
          {icon:"/static/good_book1.png",name:"窗边的小豆豆",writer:"黑柳彻子",readPeopleNum:"999+"},
          {icon:"/static/good_book2.png",name:"爱的教育",writer:"爱迪蒙托.德.亚米美斯",readPeopleNum:"999+"},
          {icon:"/static/good_book3.png",name:"夏洛的网",writer:"E.B.怀特",readPeopleNum:"823"},
          {icon:"/static/good_book4.png",name:"雾都孤儿",writer:"查尔斯.狄更斯",readPeopleNum:"417"},
          {icon:"/static/good_book5.png",name:"解忧杂货店",writer:"东圭野吾",readPeopleNum:"392"},
        ],
        classes:[
          {className:"全部类型"},
          {className:"图画文学类"},
          {className:"童话类"},
          {className:"小说类"},
          {className:"科学文艺类"},
          {className:"故事类"},
          {className:"寓言类"},
          {className:"儿童曲艺类"},
        ]
      }
    },
    methods:{
      downToogle(){
        $(".down-box").slideToggle(300);
        $(".mask").fadeToggle(300);
      },
      selectClass(i){
        this.selectedClass=this.classes[i].className;
        this.downToogle();
      },
      toBookDetail(i){
        sessionStorage.bookId=this.books[i].id;
        this.$router.push("/bookdetail")
      }
    }
  }

</script>

<style scoped>
  .container{
    width: 100%;
    height: 100%;
    background-color: white;
    display: flex;
    flex-direction: column;
    align-items: center;
    overflow: hidden;
  }
  .body{
    width: 100%;
    height: 100%;
    overflow: auto;
  }
  .mask{
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: rgba(217, 217, 217, 0.41);
  }
  .down-box{
    width: 100%;
    background-color: #f1f9fc;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    padding: 10px 0;
  }
  .filter{
    width: 100%;
    background-color: #bbe0f2;
    padding: 10px 0;
    display: flex;
    flex-direction: row;
    align-items: center;
  }
  .filter-down, .filter-read, .filter-postil{
    flex: 1;
    font-size: 0.9em;
    color: white;
  }
  .bookList{
    width: 95%;
    display: flex;
    flex-direction: row;
    align-items: center;
    border-bottom: 1px solid #dddddd;
  }
  .left{
    width: 70px;
    height: 90px;
    margin: 10px;
  }
  .mid{
    flex: 1;
    display: flex;
    flex-direction: column;
    text-align: left;
    margin-left: 10px;
  }
  .right{
    margin: 10px;
    background-color: #69aae6;
    border: none;
    color: white;
    padding: 5px 15px;
    height: 30px;
    border-radius: 15px;
  }
  .right:focus{
    outline: none;
  }
  .right:active{
    background-color: #79c0e6;
  }

</style>



