<template>

  <div class="container">
    <my-title title="全部活动" rightImg="/static/home_find.png"></my-title>
    <!--筛选框-->
    <div class="filter">
      <div class="filter-down" @click="downToogle">{{selectedState}}</div>
      <div class="filter-read">最新发布</div>
      <div class="filter-postil">最多参与</div>
    </div>
    <!---->
    <div class="body">

      <!--蒙砂-->
      <div class="mask" @click="downToogle">
        <!--下拉框-->
        <div class="down-box">
          <div class="class-item"
               v-for="(item,index) in states"
               @click.stop="selectClass(index)">
            <div style="margin-left: 10px;">{{item.className}}</div>
            <div style="flex: 1;"></div>
            <div style="margin-right: 10px;">√</div>
          </div>
        </div>
      </div>
      <!--活动列表-->
      <div class="bookList" v-for="(activity,index) in activities"
           v-if="selectedState=='全部状态' || states[activity.state].className==selectedState">
        <!--左边图片-->
        <img class="left" :src="activity.icon">

        <!--中间内容-->
        <div class="mid">
          <div style="font-size: 0.9em;color: #222">{{activity.title}}</div>
          <div style="font-size: 0.8em;margin-top: 5px;color: #989898">《{{activity.name}}》</div>
          <div style="font-size: 0.7em;margin-top: 10px;color: #84cdf3">已有{{activity.people_join}}人参与</div>
        </div>

        <!--右边按钮-->
        <button class="right" @click="toBookDetail(index)" :style="{background:states[activity.state].color}">
          {{states[activity.state].className}}
        </button>

      </div>

    </div>

  </div>

</template>

<script>
  import MyTitle from '@/components/common/MyTitle'
  export default {
    name: 'AllBook',
    components:{MyTitle},
    data(){
      return{
        selectedState:"全部状态",
        activities:[
          {icon:"/static/good_book1.png",title:"假如我在巴学园上学",name:"窗边的小豆豆",state:"1",people_join:"240"},
          {icon:"/static/good_book2.png",title:"为你编织童年的网",name:"爱的教育",state:"2",people_join:"407"},
          {icon:"/static/good_book1.png",title:"《窗边的小豆豆》导读",name:"窗边的小豆豆",state:"2",people_join:"1455"},
          {icon:"/static/good_book3.png",title:"如何写好成长日记？",name:"雾都孤儿",state:"3",people_join:"2958"},
          {icon:"/static/good_book1.png",title:"跟我读小豆豆",name:"窗边的小豆豆",state:"3",people_join:"1024"},
        ],
        states:[
          {className:"全部状态",color:""},
          {className:"尚未开始",color:"red"},
          {className:"正在进行",color:"green"},
          {className:"已经结束",color:"grey"},
        ]
      }
    },
    methods:{
      downToogle(){
        $(".down-box").slideToggle(300);
        $(".mask").fadeToggle(300);
      },
      selectClass(i){
        this.selectedState=this.states[i].className;
        this.downToogle();
      },
      toBookDetail(i){
        this.$router.push("/bookdetail")
      }
    }
  }

</script>

<style scoped>
  .container{
    width: 100%;
    height: 100%;
    background-color: white;
    display: flex;
    flex-direction: column;
    align-items: center;
    overflow: hidden;
  }
  .body{
    width: 100%;
    height: 100%;
    overflow: auto;
  }
  .mask{
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: rgba(217, 217, 217, 0.41);
  }
  .down-box{
    width: 100%;
    background-color: #f1f9fc;
    display: flex;
    flex-direction: column;
    padding: 10px 0;
  }
  .down-box .class-item{
    padding: 10px 5px;
    color: #3e83a9;
    font-size: 0.9em;
    text-align: left;
    display: flex;
    flex-direction: row;
    align-items: center;
  }
  .filter{
    width: 100%;
    background-color: #bbe0f2;
    padding: 10px 0;
    display: flex;
    flex-direction: row;
    align-items: center;
  }
  .filter-down, .filter-read, .filter-postil{
    flex: 1;
    font-size: 0.9em;
    color: white;
  }
  .bookList{
    width: 95%;
    display: flex;
    flex-direction: row;
    align-items: center;
    border-bottom: 1px solid #dddddd;
  }
  .left{
    width: 70px;
    height: 90px;
    margin: 10px;
  }
  .mid{
    flex: 1;
    display: flex;
    flex-direction: column;
    text-align: left;
    margin-left: 10px;
  }
  .right{
    margin: 10px;
    background-color: #69aae6;
    border: none;
    color: white;
    padding: 5px 15px;
    height: 30px;
    border-radius: 15px;
  }
  .right:focus{
    outline: none;
  }
  .right:active{
    background-color: #79c0e6;
  }

</style>



